import GraphChild from "./GraphChild.js";
import { getJSfunction } from '../global.js';

export default class Func /** or slider */ extends GraphChild {

   constructor(options) {
      //#region 
      let propName;
      propName = 'id';
      if (!options[propName]) {
         throw new Error('Your options passed to the shetchChild is not valid, it doesn\'t has ' + 'name' + ' property, or it is falsy value');
      }
      propName = 'expr';
      if (!options[propName]) {
         throw new Error('Your options passed to the shetchChild is not valid, it doesn\'t has ' + 'name' + ' property, or it is falsy value');
      }
      propName = 'params';
      if (!options[propName]) {
         throw new Error('Your options passed to the shetchChild is not valid, it doesn\'t has ' + 'name' + ' property, or it is falsy value');
      }
      //#endregion

      super(options, () => {
         if (!this.id.match(/[a-zA-Z_]+\d*/)) throw new Error(`${this.id} is invalid.`);
         this.func = getJSfunction(options.expr, options.params);
         Math[this.id] = this.func;

      });

   }

   _remove() {
      delete Math[this.id];
   }

}